  <div class="sidebar-container">
    <el-menu class="el-menu-vertical" router :collapse="false" :default-active="defaultActive">
      <el-menu-item-group v-for="(menu, index) in menus" :key="menu.path + index">
        <el-submenu :index="menu.path" v-if="menu.collapse">
          <template slot="title">
            <i class="el-icon-caret-bottom"></i>
            <span>{{ menu.title }}</span>
          </template>
          <el-menu-item-group>
            <el-menu-item v-for="(item, i) in menu.children" :key="item.path + i" :index="item.path">
              <i :class="item.icon"></i>{{ item.title }}
            </el-menu-item>
          </el-menu-item-group>
        </el-submenu>
        <template v-else>
          <template slot="title" v-if="menu.title">
            <span class="item-group-title-span">{{ menu.title }}</span>
          </template>
          <el-menu-item v-for="(item, i) in menu.children" :key="item.path + i" :index="item.path">
            <i :class="item.icon"></i>{{ item.title }}
          </el-menu-item>
        </template>
      </el-menu-item-group>
    </el-menu>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'sidebar',
  data() {
    return {};
  },
  computed: {
    ...mapGetters(['menus']),
    defaultActive() {
      let { fullPath } = this.$route;
      fullPath = fullPath.indexOf('?') !== -1 ? fullPath.split('?')[0] : fullPath;
      return (
        fullPath || (this.menus && this.menus[0] && this.menus[0].children[0] && this.menus[0].children[0].path) || ''
      );
    },
  },
  created() {},
  methods: {
    handleOpen() {},
  },
};
</script>

