<template>
  <div class="search-wraper" v-if="isVisible">
    <div class="search-bar" :style="{ width: width }">
      <svg-icon class="search-icon" @click="search" icon-class="search"></svg-icon>
      <input class="input" type="text" @focus="focus" :placeholder="placeholder" v-model="inputeValue" />
      <svg-icon class="close-icon" icon-class="close" @click="close"></svg-icon>

      <div class="quick-links" v-if="showQuickLinks && quickLinks.length > 0">
        <span class="tips"> {{ $i18n.t('home.searchQuickLinks') }} </span>
        <span v-for="(item, index) in quickLinks" :key="index" @click="linksEvent(item.value)">
          {{ item.value }}
        </span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SearchBar',
  props: {
    visible: {
      type: Boolean,
      default: true,
    },
    width: {
      type: String,
      default: '100%',
    },
    placeholder: {
      type: String,
      default: '',
    },
    quickLinks: {
      type: Array,
      default: () => {
        return [];
      },
    },
  },
  data() {
    return {
      isVisible: false,
      inputeValue: '',
      showQuickLinks: false,
    };
  },
  watch: {
    visible(n) {
      this.isVisible = n;
      this.inputeValue = '';
    },
  },
  methods: {
    close() {
      this.isVisible = false;
      this.showQuickLinks = false;
      this.$emit('close');
    },
    focus() {
      this.showQuickLinks = true;
    },
    linksEvent(val) {
      this.inputeValue = val;
      this.showQuickLinks = false;
    },
    search() {
      this.$emit('search', this.inputeValue);
    },
  },
};
</script>

<style lang="scss" scoped>
