  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
  margin: auto;
  height: 60px;
  .search-bar {
    display: flex;
    justify-content: center;
    align-items: center;
    .search-icon {
      width: 16px;
      margin-right: 4px;
      cursor: pointer;
    }
    .input {
      flex: 2;
      height: 40px;
      border: none;
      outline: none;
      padding-left: 8px;
      padding-right: 8px;
      &::-webkit-input-placeholder {
        color: #ccc;
      }
    }
    .quick-links {
      position: absolute;
      background-color: #fff;
      width: inherit;
      top: 60px;
      padding: 16px;
      padding-top: 10px;
      border-bottom-left-radius: 4px;
      border-bottom-right-radius: 4px;
      border-top: 1px solid $--fill_color--regular;
      color: $--text_color--emphasis;
      z-index: 999;
      .tips {
        color: #86909cff;
      }
      span {
        display: block;
        font-size: 14px;
        height: 34px;
        line-height: 34px;
        cursor: pointer;
      }
    }
  }
  .close-icon {
    width: 12px;
    cursor: pointer;
  }
}
</style>
