<template>
  <svg xmlns="http://www.w3.org/2000/svg" width="100%" height="100%" version="1.1" viewBox="0 0 200 200">
    <defs>
      <linearGradient id="project_icon" x1="0%" y1="0%" x2="0%" y2="100%">
        <stop offset="40%" style="stop-color: #042166; stop-opacity: 1" />
        <stop offset="80%" style="stop-color: #338bff; stop-opacity: 1" />
      </linearGradient>
    </defs>
    <rect rx="5" ry="5" width="200" height="200" fill="#D6EDFF" />
    <text x="100" y="150" font-size="124" style="fill: url(#project_icon); text-anchor: middle">{{ showText }}</text>
  </svg>
</template>

<script>

/**
 * SvgAvatar 动态 svg 头像
 * @description 组件用于生成无头像用户的默认头像
 * @property 无头像用户的名字第一个字符
 * @examplae <SvgAvatar text="A"></SvgAvatar>
 */

export default {
  name: 'SvgAvatar',
  props: {
    text: {
      default: 'A',
      type: String,
      validator(value) {
        return value.length === 1;
      },
    },
  },
  computed: {
    showText() {
      return (this.text && this.text.toUpperCase()) || '';
    },
    // 根据字符的中英文动态设置字体大小
    fontSize() {
      let reg = /^[0-9a-zA-Z]*$/g;
      return reg.test(this.text) ? [150, 150] : [145, 120];
    },
  },
};
</script>

<style lang="scss" scoped></style>
