<template>
  <div class="header-bar" :class="{ 'is-search': searchBarVisible, 'personal-settings-route': isPersonalSettings }">
    <div class="header-left">
      <img src="@/assets/img/logo.svg" alt="" class="logo-img" />
      <span class="title">{{ $i18n.t('home.orgPortal') }}</span>
    </div>

    <div class="header-mid-box">
      <div class="header-mid">
        <el-menu
          v-show="showMeun && !searchBarVisible"
          router
          @select="handleSelect"
          :default-active="defaultActive"
          :active-text-color="isHome ? '#fff' : GlobalStyleVariables.activeMenuColor"
          mode="horizontal"
          background-color="transparent"
        >
          <el-menu-item
            :index="item.path"
            v-for="(item, i) in defaultMenus"
            :key="item.path + i"
            :disabled="item.disabled"
            >{{ item.title }}</el-menu-item
          >
        </el-menu>

        <div v-show="showMeun && isMoreBtnShow && !searchBarVisible" class="more-btn-box">
          <el-popover
            placement="bottom"
            width="auto"
            trigger="click"
            :offset="60"
            v-model="showMoreMenus"
            popper-class="more-menus-popover"
            :visible-arrow="false"
          >
            <el-menu
              router
              @select="handleSelect"
              :default-active="defaultActive"
              :active-text-color="GlobalStyleVariables.activeMenuColor"
              mode="vertical"
              background-color="transparent"
            >
              <el-menu-item
                v-for="(item, i) in moreMenus"
                :key="item.path + i"
                :index="item.path"
                :disabled="item.disabled"
                >{{ item.title }}</el-menu-item
              >
            </el-menu>
            <svg-icon slot="reference" icon-class="ellipsis" class="more-icon"></svg-icon>
          </el-popover>
        </div>

        <search-bar
          ref="searchBar"
          :visible="searchBarVisible"
          width="500px"
          :placeholder="$i18n.t('home.searchBarPlaceholder')"
          :quickLinks="quickLinks"
          @close="searchBarVisible = false"
        ></search-bar>
      </div>
    </div>

    <div class="header-right">
      <div class="search-icon-bg">
        <svg-icon @click="searchBarVisible = true" icon-class="search" class="icon"></svg-icon>
      </div>
      <div class="bell-icon-bg">
        <svg-icon icon-class="bell" class="icon"></svg-icon>
      </div>
      <el-dropdown trigger="click" @command="handleCommand">
        <el-avatar shape="circle" :size="32">
          <svg-avator :text="currentUsername[0] || 'A'" v-if="currentUsername"></svg-avator>
        </el-avatar>
        <el-dropdown-menu slot="dropdown" class="personal-dropdown">
          <el-dropdown-item command="personalSettings">{{ $i18n.t('home.myAccount') }}</el-dropdown-item>
          <el-dropdown-item command="logout">{{ $i18n.t('home.logout') }}</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <div :class="{ mask: true, show: searchBarVisible }" @click="maskClick"></div>
  </div>
</template>

<script>
import SearchBar from '../search-bar';
import SvgAvator from '@/components/svg-avatar/index.vue';
import { mapGetters } from 'vuex';
const MIN_W = 480; // 最小Menu宽度
const ONCE_MENU_W = 126; // 每个菜单的宽度

export default {
  name: 'OrgHeader',
  components: {
    SearchBar,
    SvgAvator,
  },
  props: {
    showMeun: {
      type: Boolean,
      default: true,
    },
    isPersonalSettings: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    ...mapGetters(['activeMenu', 'topMenus', 'currentUsername']),
    defaultMenus() {
      const { topMenus, showMeunNum } = this;
      return topMenus.slice(0, showMeunNum);
    },
    moreMenus() {
      const { topMenus, moreMenuNum } = this;
      return moreMenuNum ? topMenus.slice(-moreMenuNum) : [];
    },
    isMoreBtnShow() {
      const { moreMenuNum } = this;
      return moreMenuNum > 0;
    },
    moreMenuNum() {
      const { topMenus, showMeunNum } = this;
      let len = topMenus.length;
      return len - showMeunNum;
    },
    isHome() {
      return ['/home'].includes(this.$route.path);
    },
  },
  data() {
    return {
      searchBarVisible: false,
      quickLinks: [
        { value: 'Content1' },
        { value: 'Content2' },
        { value: 'Content3' },
        { value: 'Content4' },
        { value: 'Content5' },
      ],
      userName: 'Jenny',
      defaultActive: '/home',
      showMoreMenus: true,
      showMeunNum: 8,
    };
  },
  filters: {
    getFirstStr(value) {
      return value.charAt(0);
    },
  },
  created() {
    this.$store.dispatch('routerStore/getUserPermData');
  },
  mounted() {
    this.handleSelect();
    let len = this.topMenus.length;
    this.showMeunNum = len;
    this.initMenus(len);
    window.onresize = () => {
      this.initMenus(len);
    };
  },
  methods: {
    initMenus(len) {
      let val = window.innerWidth;
      if (val >= ONCE_MENU_W * (len + 1) + MIN_W) {
        this.showMeunNum = len;
      } else if (val <= MIN_W + ONCE_MENU_W) {
        this.showMeunNum = 1;
      } else {
        this.showMeunNum = parseInt((val - MIN_W) / ONCE_MENU_W);
      }
    },
    maskClick() {
      this.$refs.searchBar.close();
    },
    handleSelect(path) {
      this.showMoreMenus = false;
      const { fullPath } = this.$route;
      path = path || fullPath;
      let activeMenu = path.split('/')[2] || path.split('/')[1];
      let activeTopMenus = this.topMenus.filter((tf) => {
        return tf.path && tf.path.indexOf(activeMenu) !== -1;
      });
      this.defaultActive = activeTopMenus[0] && activeTopMenus[0].path;
      if (activeMenu && this.activeMenu !== activeMenu) {
        this.$store.dispatch('routerStore/setMeuns', { activeMenu });
      }
    },
    handleCommand(command) {
      if (command === 'personalSettings') {
        this.$router.push({
          path: '/personalSettings',
        });
      } else {
        this.$store.dispatch('baseStore/setLogoutVisible', true);
      }
    },
  },
};
</script>

