<template>
  <div>
    <div v-if="isPersonalSettings" class="personal-settings-bg"></div>
    <div :class="['header', isPersonalSettings ? 'personal-settings-route' : '']">
      <org-header :showMeun="showMeun" :isPersonalSettings="isPersonalSettings"></org-header>
    </div>
    <div class="layout-container">
      <transition name="load-transform" mode="out-in">
        <aside class="sidebar" v-show="hiddenSidebar">
          <sidebar></sidebar>
        </aside>
      </transition>
      <div class="app-main" :class="{ 'home-page-container': !hiddenSidebar, 'has-left-sidebar': onlyContainerPage }">
        <app-main></app-main>
      </div>
    </div>
    <portal-dialog
      width="448px"
      :title="$t('home.logout')"
      :visible="logoutVisible"
      @confirm-btn-click="confirmHandle"
      @cancel-btn-click="cancelHandle"
      @closed-dialog="cancelHandle"
    >
      <div slot="content" class="logout-content">
        <span class="title">{{ $t('home.sureLogout') }}</span>
      </div>
    </portal-dialog>
  </div>
</template>

<script>
import orgHeader from '@/layout/components/header';
import sidebar from '@/layout/components/sidebar';
import appMain from '@/layout/components/app-main';
import PortalDialog from '@/components/portal-dialog';
import { logout } from '@/views/auth/api';
import { mapGetters } from 'vuex';

export default {
  name: 'mainLayout',
  props: {
    isNewWindows: {
      type: Boolean,
      default: true,
    },
  },
  components: {
    orgHeader,
    sidebar,
    appMain,
    PortalDialog,
  },
  computed: {
    ...mapGetters(['logoutVisible']),
    showMeun() {
      let { matched = [] } = this.$route;
      let rotueMeta = (matched[0] && matched[0].meta) || {};
      if (this.checkWindowsPath()) {
        return false;
      }
      return rotueMeta.showMeun;
    },
    hiddenSidebar() {
      let { matched = [] } = this.$route;
      let rotueMeta = (matched[1] && matched[1].meta) || {};
      if (this.checkWindowsPath()) {
        return false;
      }
      return !rotueMeta.hiddenSidebar;
    },
    onlyContainerPage() {
      let { meta = {} } = this.$route;
      return meta.hasLeftSidebar;
    },
    key() {
      return this.$route.path;
    },
    isPersonalSettings() {
      return ['/home', '/personalSettings'].includes(this.$route.path);
    },
  },
  mounted() {
    this.escHandle();
  },
  methods: {
    escHandle() {
      document.addEventListener('keydown', (e) => {
        if (e.keyCode === 27) {
          e.stopPropagation();
          this.$store.dispatch('baseStore/setLogoutVisible', false);
        }
      });
    },

    checkWindowsPath() {
      let { meta = {} } = this.$route;
      return meta.isNewWindowOpen;
    },
    cancelHandle() {
      this.$store.dispatch('baseStore/setLogoutVisible', false);
    },
    confirmHandle() {
      logout().then((res) => {
        if (res && res.header.success) {
          this.$router.push({ path: '/login' });
          this.$store.dispatch('baseStore/setLogoutVisible', false);
        }
      });
    },
  },
};
</script>

